import imgCode from '@/api/globalImages.js'
import {mapActions, mapMutations, mapState} from "vuex";
export default {
    props:['newCourses','hotCourse'],
	data() {
		return {
            courseLevel:'',
            querynew:{
                pageNum: 1,
                pageSize: 8,
                entity: {}
            },
            queryhot:{
                pageNum: 1,
                pageSize: 6,
                entity: {}
            },
            token:'',
            imgUrl:'',
            tokens:'',
            goodBook:['/image/book1.png','/image/book2.png','/image/book3.png','/image/book4.png','/image/book5.png']

        }
	},
    created(){
        //this.tokens = localStorage.getItem('token')
        this.getImageByCode()
    },
    computed: {
        ...mapState({
            userInfo: (state) => state.user.userInfo,
            isLogin: (state) => state.user.isLogin,
        }),
    },
    methods:{
        ...mapActions(["saveCartNumAction"]),
        ...mapMutations(["saveLoginDialog"]),
        getImageByCode(){
            this.$getImageByCode({imageCode:imgCode.global_commendcourse}).then(res => {
                //
                this.imgUrl = res.data.data.imageUrl;
            })
        },
        //跳转到课程页面
        goCourse(){
            this.$router.push('/course')
        },
    }
}
