import Verify from '~/components/verifition/Verify'
import { Loading } from 'element-ui'
import { Encrypt, Decrypt } from '@/utils/aes.js'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
	data() {
		return {
      isVerify:false,
			rememberMe: false, // 是否点击了记住我 用户名密码登录记住我
			identifiyRememberMe: false, // 手机号登录记住我
			courseColor: -1, // 鼠标移上显示颜色
			crtType: 'usernamePasswordLogin',
			regiterSuccess: false, // 对话框
			checked: false, // 同意协议
			actives: '1',
			msg: '我是头部',
			// carNum: 0,
			isCar: false,
			// isLogin: false,
			isUserInfo: false,
			avatorImg: '/image/common/avator.png',
			nickname: '小鹿线-默认',
			// userInfo: null,
			loginNav: [
				{
					id: 0,
					title: '账号登录',
				},
				{
					id: 1,
					title: '验证码登录',
				},
			],
			loginCurrent: 0,
			isregister: false,
			isSend: false,
			registerForm: {}, // 注册
			captcha: '短信验证码',
			registerRules: {
				mobile: [
					{
						required: true,
						message: '请输入手机号',
						trigger: 'blur',
					},
					{
						pattern: /^1[3456789]\d{9}$/,
						message: '目前只支持中国大陆的手机号码',
					},
				],
				captcha: [
					{
						required: true,
						message: '请输入验证码',
						trigger: 'blur',
					},
				],
			}, // 注册
			phoneForm: {
				username:'',
				password:'',
				rememberMe: false
			}, // 账号登陆
			phoneRules: {
				username: [
					{
						required: true,
						message: '请输入用户名',
						trigger: 'blur',
					},
				],
				password: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
				],
			}, // 账号
			identifyForm: {
				mobile:'',
				captcha:'',
				identifiyRememberMe:false,
			}, // 验证码登陆
			identifyRules: {
				mobile: [
					{
						required: true,
						message: '请输入手机号',
						trigger: 'blur',
					},
					{
						pattern: /^1[3456789]\d{9}$/,
						message: '目前只支持中国大陆的手机号码',
					},
				],
				captcha: [
					{
						required: true,
						message: '请输入验证码',
						trigger: 'blur',
					},
				],
			},
			avatorList: [
				{
					id: 1,
					imgUrl: '/image/header/course.png',
					title: '我的课程',
					linkUrl: '/about/my-course',
				},
				{
					id: 2,
					imgUrl: '/image/header/order.png',
					title: '订单中心',
					linkUrl: '/about/order',
				},
				{
					id: 3,
					imgUrl: '/image/header/mess.png',
					title: '我的消息',
					linkUrl: '/about/message',
				},
				{
					id: 4,
					imgUrl: '/image/header/setting.png',
					title: '个人设置',
					linkUrl: '/user/setbindsns',
				},
			],
			keywords: '',
			phonetimer: null,
			registerTiemr: null,
			Phonecaptcha: '短信验证码',
			phoneSend: false,
      vipInfos:{},
      vipEndtime:'',
      userServiceAgreement: {
        id: "",
        title: "",
        content: "",
        code: ""
      },
      privateAgreement:{
        id: "",
        title: "",
        content: "",
        code: ""
      },
		}
	},
	computed: {
		...mapState({
			userInfo: (state) => state.user.userInfo,
			isLogin: (state) => state.user.isLogin,
			cartNum: (state) => state.user.cartNum,
			loginDialog: (state) => state.user.loginDialog,
      token:state=>state.user.token
		}),
	},
	created() {
		if( this.token ){
			// 获取购车数据
		  this.getCarNum();
		  this.getUserInfo()
		}
		// 获取搜索框数据
		this.copySearch();
    //隐私政策和服务协议
    this.getServiceAgreement("6HG6326I");//
    this.getPrivateAgreement("6GFL2QGQ");//
	},
  mounted(){
    this.isVerify = true;
  },
	components: {
		Verify,
	},
	methods: {
		...mapActions([
			'saveUserInfoAction',
			'saveLoginAction',
			'saveCartNumAction',
		]),
		...mapMutations(['saveLoginDialog','setToken','removeUser']),
    //获取服务协议
    getServiceAgreement(code){
      this.$getAgreementByCode(code).then(res => {
        if(res.meta.code === '200'){
          this.userServiceAgreement = res.data.data
        }
      })
    },
    //获取隐私协议
    getPrivateAgreement(code){
      this.$getAgreementByCode(code).then(res => {
        if(res.meta.code === '200'){
          this.privateAgreement = res.data.data
        }
      })
    },
    //跳转到隐私页面
    goAgreement(code){
      this.$router.push({
        path: '/agreement',
        query: {
          code: code,
        }
      })
    },
		// 微信登录
		goWeixin() {
            //'https://4147551eu3.qicp.vip/oauth/login/WECHAT_OPEN'
            // 'https://www.xuexiluxian.cn/api/oauth/login/WECHAT_OPEN'
			window.location.href ='https://www.xuexiluxian.cn/api/oauth/login/WECHAT_OPEN'
		},
        goQq() {
            //'https://4147551eu3.qicp.vip/oauth/login/WECHAT_OPEN'
            // 'https://www.xuexiluxian.cn/api/oauth/login/WECHAT_OPEN'
			window.location.href ='https://www.xuexiluxian.cn/api/oauth/login/qq'
		},
        // 微博登录
        goWeibo(){
            window.location.href ='https://www.xuexiluxian.cn/api/oauth/login/weibo'
        },
		// 行为验证码
		success(e) {
			switch (this.crtType) {
				// 用户名登录
				// 手机号登录
				case 'mobileCaptchaLogin':
					this.identifyForm.captchaVerification =
						e.captchaVerification
					this.sendLoginCode()
					break
				// 注册
				case 'registerLogin':
					this.registerForm.captchaVerification =
						e.captchaVerification
					this.sendCaptch()
					break
			}
		},
		error(e) {
			this.$message({
				message: '验证失败，请重新验证',
				type: 'error',
			})
		},
		showVerify(formName) {
			if (this.registerForm.mobile || this.identifyForm.mobile) {
				let reg = /^1[3456789]\d{9}$/
				if (
					reg.test(this.registerForm.mobile) ||
					reg.test(this.identifyForm.mobile)
				) {
					this.$refs.verify.show()
				} else {
					this.$message({
						message: '手机号非法',
						type: 'warning',
					})
				}
			} else {
				this.$message({
					message: '手机号为空',
					type: 'warning',
				})
			}
		},
		// 清空表单
		goReset() {
			this.phoneForm = {
				username:'',
				password:'',
				rememberMe: false
			}
			this.registerForm = {}
			this.identifyForm = {
				mobile:'',
				captcha:'',
				identifiyRememberMe:false,
			}
		},
		// 点击开始学习
		goStudy() {
			this.isregister = false
			this.regiterSuccess = false
			//this.loginDialog = true
			this.$store.commit('saveLoginDialog', true)
		},
		// 注册成功弹出
		handleRegiterClose() {
			this.regiterSuccess = false
			this.$store.commit('saveLoginDialog', false)
			//this.loginDialog = false
		},
		//点击图标返回首页
		goHome() {
			this.$router.push('/')
		},
		//关键字搜索
		toSearch() {
			if (!this.keywords) {
				this.$message({
					message: '请输入关键字进行搜索！',
					type: 'error',
				})
				return
			}
			this.$router.replace({
				path: '/course',
				query: { keywords: this.keywords },
			})
		},
		// 去我的课程s
		goAbout() {
			this.$router.push('/about/my-course')
		},
		// 打开对话框
		goLogin() {
			this.goReset()
			this.$store.commit('saveLoginDialog', true)
		},
		// 关闭对话框
		handleClose() {
			this.goReset()
			this.$store.commit('saveLoginDialog', false)
			clearInterval(this.phonetimer)
			clearInterval(this.registerTiemr)
		},
		// 确认注册
		submitRegisterForm(formName) {
			if (this.checked) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						var regiterloading = Loading.service({
							lock: true,
							text: 'Loading',
							spinner: 'el-icon-loading',
							background: 'rgba(0, 0, 0, 0.7)',
						})
						let mobile = Encrypt(this.registerForm.mobile)
						let captchaVerification = this.registerForm
							.captchaVerification
						this.$register({
							mobile,
							captcha: this.registerForm.captcha,
							captchaVerification,
						})
							.then((res) => {
								if (res.meta.code == '200') {
									this.$message({
										message: '注册成功',
										type: 'success',
									})
									this.isregister = false
									this.regiterSuccess = true
									this.saveLoginAction()
									// this.$store.commit("saveLoginDialog", false);
									this.$nextTick(() => {
										// 以服务的方式调用的 Loading 需要异步关闭
										regiterloading.close()
									})
									this.$store.commit('saveLoginDialog', false)
								} else if (res.meta.code == '10005') {
									this.$message({
										message: res.meta.msg,
										type: 'info',
									})
									this.isregister = false
									this.$nextTick(() => {
										// 以服务的方式调用的 Loading 需要异步关闭
										regiterloading.close()
									})
								} else {
									this.$message({
										message: res.meta.msg,
										type: 'error',
									})
									this.isregister = false
									this.$nextTick(() => {
										// 以服务的方式调用的 Loading 需要异步关闭
										regiterloading.close()
									})
								}
							})
							.catch((err) => {
								this.$message({
									message: res.meta.msg,
									type: 'error',
								})
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									regiterloading.close()
								})
							})
					} else {
						return false
					}
				})
			} else {
				this.$message({
					message: '请勾选同意隐私协议',
					type: 'error',
				})
			}
		},
		// 用户名和密码登陆成功
		submitPhoneForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					// alert('submit!');
					var phoneloading = Loading.service({
						lock: true,
						text: 'Loading',
						spinner: 'el-icon-loading',
						background: 'rgba(0, 0, 0, 0.7)',
					})
					let username = Encrypt(this.phoneForm.username);
					let password = Encrypt(this.phoneForm.password);
					let captchaVerification = this.phoneForm.captchaVerification
					this.$loginByJson({
						username,
						password,
						captchaVerification,
					})
						.then((res) => {
							if (res.meta.code === '10006') {
								// 存储token
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									phoneloading.close()
								})
								this.$store.commit('saveLoginDialog', false);
								let accessToken = res.data.accessToken;
								// 存储到access中
                this.setToken(Encrypt(accessToken));
								this.saveLoginAction()
								this.getCarNum()
								this.getUserInfo()
								this.$message({
									message: '登录成功，赶紧去学习吧！',
									type: 'success',
								})
								this.$router.go(0)
								// window.location.reload()
							}else {
								this.$message({
									message: res.meta.msg,
									type: 'error',
								})
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									phoneloading.close()
								})
								this.$store.commit('saveLoginDialog', false)
							}
						})
						.catch((err) => {
							this.$nextTick(() => {
								// 以服务的方式调用的 Loading 需要异步关闭
								phoneloading.close()
							})
							this.$message({
								message: res.meta.msg,
								type: 'error',
							})
						})
				} else {
					return false
				}
			})
		},
		// 验证是否点击7天登录
		// 验证码登陆
		submitIdentifyForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					var identLoading = Loading.service({
						lock: true,
						text: 'Loading',
						spinner: 'el-icon-loading',
						background: 'rgba(0, 0, 0, 0.7)',
					})
					// alert('submit!');
					let mobile = Encrypt(this.identifyForm.mobile)
					let captchaVerification = this.identifyForm
						.captchaVerification
					this.$loginByMobile({
						mobile,
						captcha: this.identifyForm.captcha,
						captchaVerification,
					})
						.then((res) => {
							if (res.meta.code === '10006') {
								// 存储token
								let accessToken = res.data.accessToken
								// 存储到access中
								this.setToken(Encrypt(accessToken));
								// 获取个人信息
								this.getUserInfo()
								// 获取购物车数据
								this.getCarNum()
								//  this.saveIsLoginAction(true)
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									identLoading.close()
								})
								this.$store.commit('saveLoginDialog', false)
								this.$message({
									message: '登录成功，赶紧去学习吧！',
									type: 'success',
								})
								 this.$router.go(0)
								// window.location.reload()
							} else if (res.meta.code === '10010') {
								this.$message({
									message: res.meta.msg,
									type: 'error',
								})
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									identLoading.close()
								})
                                clearInterval(this.registerTiemr)
							this.captcha = '发送验证码'
							this.isSend = false
								// 去快速注册界面
								this.backRegiter()
								// this.$store.commit("saveLoginDialog", false);
								// this.saveLoginAction();
							}else {
								this.$message({
									message: res.meta.msg,
									type: 'error',
								})
								this.$nextTick(() => {
									// 以服务的方式调用的 Loading 需要异步关闭
									identLoading.close()
								})
              clearInterval(this.registerTiemr)
							this.captcha = '发送验证码'
							this.isSend = false
								this.$store.commit('saveLoginDialog', false)
								this.saveLoginAction()
							}
						})
						.catch((err) => {
							this.$nextTick(() => {
								// 以服务的方式调用的 Loading 需要异步关闭
								identLoading.close()
							})
							this.$message({
								message: res.meta.msg,
								type: 'error',
							})
              clearInterval(this.registerTiemr)
							this.captcha = '发送验证码'
							this.isSend = false
						})
				} else {
					return false
				}
			})
		},

		// 发送注册验证码
		sendCaptch() {
			let reg = /^1[3456789]\d{9}$/
			if (!reg.test(this.registerForm.mobile)) {
				this.$message({
					message: '手机号输入错误，请检查',
					type: 'warning',
				})
			} else {
				if (this.registerForm.mobile) {
					let mobile = this.registerForm.mobile
					// this.isSend = true;
					this.phoneSend = true
					this.Phonecaptcha = '重新发送60秒'
					this.sendCode(mobile)
					let time = 60
					clearInterval(this.phonetimer)
					this.phonetimer = setInterval(() => {
						time--
						if (time <= 0) {
							clearInterval(this.phonetimer)

							this.captcha = '发送验证码'
							this.isSend = false
							time = 60
						} else {
							this.Phonecaptcha = `重新发送${time}秒`
						}
					}, 1000)
				} else {
					this.$message({
						message: '请先填写手机号哟',
						type: 'warning',
					})
				}
			}
		},
		// 登录验证码3
		sendLoginCode() {
			let reg = /^1[3456789]\d{9}$/
			if (!reg.test(this.identifyForm.mobile)) {
				this.$message({
					message: '手机号输入错误，请检查',
					type: 'warning',
				})
			} else {
				if (this.identifyForm.mobile) {
					let mobile = this.identifyForm.mobile
					this.isSend = true
					this.captcha = '重新发送60秒'
					this.sendCode(mobile)
					let time = 60
					let timer
					clearInterval(this.registerTiemr)
					this.registerTiemr = setInterval(() => {
						time--
						if (time <= 0) {
							clearInterval(this.registerTiemr)

							this.captcha = '发送验证码'
							this.isSend = false
							time = 60
						} else {
							this.captcha = `重新发送${time}秒`
						}
					}, 1000)
				} else {
					this.$message({
						message: '请先填写手机号哟',
						type: 'warning',
					})
				}
			}
		},
		// 发送请求
		sendCode(mobile) {
			this.$sendRegisterOrLoginCaptcha({
				mobile: mobile,
			})
				.then((res) => {
					if (res.meta.code === '200') {
						this.$message({
							message: '发送成功',
							type: 'success',
						})
					} else {
						this.$message({
							message: res.meta.msg,
							type: 'error',
						})
					}
				})
				.catch((err) => {
					this.$message({
						message: err.meta.code,
						type: 'error',
					})
				})
		},
		// 获取个人信息
		getUserInfo(params) {
			this.$createToken().then((ress) => {
				this.$getInfo({
					token: ress.data.token,
				})
					.then((res) => {
						// this.saveUserInfoActions()
						if (res.meta.code === '200') {

              this.vipInfos = res.data.data.vipInfo
              if( this.vipInfos ){
                var now = new Date().getTime()
                var num = this.vipInfos.endTime - now
                this.vipEndtime = Math.floor(num / 1000 / 60 / 60 / 24)
                // this.vipEndtime = -100
              }/*else if(this.vipEndtime < 0){
                Cookies.set('vipEndtime', Math.abs(this.vipEndtime), {
                  expires: 1,
                });
              }*/
							// localStorage.setItem('userInfo',Encrypt(JSON.stringify(res.data.data)))
							this.saveUserInfoAction(res.data.data)
							// this.$router.go(0)
							//this.$router.push('/user/setbindsns')
							// window.location.reload()
						} else {
							this.$message({
								message: res.meta.msg,
								type: 'error',
							})
						}
					})
					.catch((err) => {})
			})
		},
		// 获取购物车数据
		getCarNum() {
			if (this.token) {
				this.$getShopCarCounter().then((res) => {
					if (res.meta.code == '200') {
						this.saveCartNumAction(res.data.counter)
					} else {
						this.$message({
							message: res.meta.msg,
							type: 'error',
						})
					}
				})
			}
		},
		// 返回登陆页面
		backLogin() {
			this.isregister = false
			this.goReset()
			if (this.loginCurrent === 1) {
				this.crtType = 'mobileCaptchaLogin'
			} else {
				this.crtType = 'usernamePasswordLogin'
			}
		},
		// 去快速注册页面
		backRegiter() {
			this.isregister = true
			this.goReset()
			this.crtType = 'registerLogin'
		},
		// 登陆页面 切换
		gochange(index) {
			this.goReset()
			if (index === 1) {
				this.crtType = 'mobileCaptchaLogin'
			} else if (index === 0) {
				this.crtType = 'usernamePasswordLogin'
			}
			// this.registerForm = {};
			//
			this.loginCurrent = index
		},
		// 退出登录
		async goLogout() {
			this.$confirm('您确定要退出登录吗？', '提示信息', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning',
			})
				.then(() => {
					this.$logout()
						.then((res) => {
							this.$message({
								type: 'success',
								message: '退出成功!',
							})
              this.removeUser();
							this.$router.push("/");
							this.saveLoginAction(false);
						})
						.catch((err) => {})
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '已取消退出',
					})

					return
				})
		},
		//给搜索赋值
		copySearch() {
			this.keywords = this.$route.query.keywords
		},
		// 去课程页
		goCourse() {
			// 去课程页
			this.$router.push({
				path: '/course',
			})
		},
		//去购物车
		goShopCart() {
			this.$router.push({
				path: '/cart',
			})
		},
		// 划过头像显示
		changeColor(index) {
			this.courseColor = index
		},
		deleteColor() {
			this.courseColor = -1
		},
    open(a) {
      if( a == 'false' ){
        this.$alert('<strong>这是 <i>HTML</i> 片段</strong>', 'HTML 片段', {
          dangerouslyUseHTMLString: true
        });
      }
    }

	},
	watch: {
		$route: {
			immediate: true,
			handler(to, from) {

				let newUrl = to.fullPath
				to.fullPath.replace(/(.*)\?/, function(a, b) {
					newUrl = b
				})
				if (newUrl === '/home') {
					this.actives = '1'
				} else if (newUrl === '/course' || newUrl.startsWith('/course-info') ) {
					this.actives = '2'
				} else if (newUrl === '/member') {
					this.actives = '3'
				}
				this.copySearch()
			},
		},
	},
}
