import courseType from './courseType.vue';
import { mapState, mapActions, mapMutations } from 'vuex';
import { Encrypt } from '@/utils/aes';

export default {
  props:['categorys'],
  data() {
    return {
      msg: '首页导航和轮播组件',
      categorysDetail: [],
      sliders: [],
      arrcourse: [],//课程信息
      tagarr: [],//标签数组
      querycourse: {
        pageNum: 1,
        pageSize: 4,
        entity: {
          firstCategory: ''
        }
      },
      token: '',
      tokens: ''
    };
  },
  created() {
    //this.tokens = localStorage.getItem('token');
    this.getSliders();
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.user.userInfo,
      isLogin: (state) => state.user.isLogin,
    }),
  },
  methods: {
    ...mapActions(['saveCartNumAction']),
    ...mapMutations(['saveLoginDialog']),
    goCourseInfo(item) {
      this.$router.push('/course-info/' + item.id);
    },
    goDetail(tagName) {
      let tag = encodeURI(tagName);
      this.$router.push('/course?tagName=' + tag);
    },
    //加入购物车
    addCart(item) {
      if (!this.tokens) {
        this.$message({
          message: '请先登录才能加入购物车哦',
          type: 'error'
        });
        this.$store.commit('saveLoginDialog', true);
        return;
      }
      this.$createToken()
          .then(res => {
            this.token = res.data.token;
            this.memberId = this.userInfo.id;
            this.$addShopCar({
              courseId: item.id,
              memberId: this.memberId,
              token: this.token
            })
                .then(res => {
                  if (res.meta.code === '200') {
                    this.$getShopCarCounter()
                        .then((res) => {
                          if (res.meta.code == '200') {
                            this.saveCartNumAction(res.data.counter);
                          } else {
                            this.$message({
                              message: res.meta.msg,
                              type: 'error',
                            });
                          }
                        });
                    this.$message({
                      message: '恭喜你，加入购物车成功',
                      type: 'success'
                    });
                  } else if (res.meta.code === '20003') {
                    this.$message({
                      message: '该商品已在购物车，请勿重复添加',
                      type: 'error'
                    });
                  }
                });
          });

    },
    // 课程分类，鼠标进入移出事件
    mourseHover(item, index) {
      this.$set(this.categorysDetail, index, true);
      this.querycourse.entity.firstCategory = item.id;
      this.queryCourseTag();
      this.queryCourse();
    },
    mourseOut(index) {
      this.$set(this.categorysDetail, index, false);
    },
    // 获取轮播图
    async getSliders() {
      let res = await this.$axios({
        url: '/api/slider/getSliders',
        method: 'GET',
        header: {
          'Content-Type': 'application/json',
        },
      });
      this.sliders = res.data.list;
    },
    //查询课程
    queryCourse() {
      this.$queryCourse(this.querycourse)
          .then(res => {
            this.arrcourse = res.data.pageInfo.list;
            this.arrcourse.forEach(item => {
              switch (item.courseLevel) {
                case 1:
                  item.courseLevel = '初级';
                  break;
                case 2:
                  item.courseLevel = '中级';
                  break;
                case 3:
                  item.courseLevel = '高级';
                  break;
                default:
                  item.courseLevel = '';
              }
            });
          });
    },
    //获取课程标签
    queryCourseTag() {
      this.$queryCourseTag(this.querycourse)
          .then(res => {
            this.tagarr = res.data.pageInfo.list;
          });
    }
  },
  components: {
    courseType,
  },
};
