import { mapState, mapActions, mapMutations } from 'vuex'


export default {
	props:['courseInfoArr','courseDetail','courseChapters','downsource','courseTeacher'],
	data() {
		return {
			BASE_URL: process.env.VUE_APP_BASE_API,
			courseId: this.$route.params.courseId,
			token: '',
			memberId: '',
			tokens: '', //登录的token
			activeName: 'first',
			activeChange: 1,
            downloadIndex: 0,
            downloadMap: new Map(),
            downloadDialog: false
		}
	},
	created() {
		this.tokens = this.$cookies.get('token');
	},
	computed: {
		...mapState({
			userInfo: (state) => state.user.userInfo,
			isLogin: (state) => state.user.isLogin,
		}),
	},
	methods: {
		...mapActions(['saveCartNumAction']),
		...mapMutations(['saveLoginDialog']),
		//点击免费课或者会员课程进行搜索
		goCourseSearch(type) {
			localStorage.setItem('serarch:course:type', type)
			this.$router.push('/course')
		},
		//改变章节和资料的状态
		change1() {
			this.activeChange = 1
		},
		change2() {
			this.activeChange = 2
		},
		//跳转到订单页面
		goOrder(item) {
			if (!this.tokens) {
				this.$message({
					message: '请先登录才能购买哦',
					type: 'error',
				})
				this.$store.commit('saveLoginDialog', true)
			} else {
				let arr = new Array()
				arr.push({ number: 1, id: item.id })
				localStorage.setItem('selectedArr', JSON.stringify(arr))
				this.$router.push('/confirmOrder')
			}
		},
		//下载资料
		downloadSource(item) {
			if (!this.tokens) {
				this.$message({
					message: '请先登录才能才能下载资料哦',
					type: 'error',
				})
				this.$store.commit('saveLoginDialog', true)
				return
			} else {
				this.$checkAuth(item.courseId).then((res) => {
					let hasAuth = res.data.data.hasAuth
					if (!hasAuth) {
						this.$message({
							message: '购买该课程后才能下载资料哦',
							type: 'error',
						})
						return
					} else {
                        this.$message({
							message: '正在下载，请稍后...',
							type: 'success',
						})
                        let that = this;
                        let courseId = item.courseId, attachmentId = item.id;
                        this.$downloadAttachment(courseId, attachmentId).then((res) => {
                            const blob = new Blob([res])
                            let fileName = item.attachmentName
                            let fileUrl = item.attachmentUrl
                            const extName = fileUrl.substring(
                                fileUrl.lastIndexOf('.')
                            )
                            fileName = fileName + extName
                            const link = document.createElement('a')
                            link.download = fileName
                            link.target = '_blank'
                            link.style.display = 'none'
                            link.href = URL.createObjectURL(blob)
                            document.body.appendChild(link)
                            link.click()
                            URL.revokeObjectURL(link.href)
                            document.body.removeChild(link)
						}).catch((e) => {
                            this.$message.error('该文件无法下载')
                        })
					}
				})
			}
		},
		//加入购物车
		addCart() {
			if (!this.tokens) {
				this.$message({
					message: '请先登录才能加入购物车哦',
					type: 'error',
				})
				this.$store.commit('saveLoginDialog', true)
				return
			}
			this.$createToken().then((res) => {
				this.token = res.data.token
				this.memberId = this.userInfo.id
				this.$addShopCar({
					courseId: this.courseId,
					memberId: this.memberId,
					token: this.token,
				}).then((res) => {
					if (res.meta.code === '200') {
						this.$getShopCarCounter().then((res) => {
							if (res.meta.code == '200') {
								this.saveCartNumAction(res.data.counter)
							} else {
								this.$message({
									message: res.meta.msg,
									type: 'error',
								})
							}
						})
						this.$message({
							message: '恭喜你，加入购物车成功',
							type: 'success',
						})
					}
				})
			})
		},
		// 课程分类，鼠标进入移出事件
		mourseHover(j) {
			j.isShow = true
		},
		mourseOut(j) {
			j.isShow = false
		},
		goPlay(courseId, chapterId, publicType) {
			if (!this.tokens) {
				this.$message({
					message: '请先登录才能学习该课程哦',
					type: 'error',
				})
				this.$store.commit('saveLoginDialog', true)
				return
			} else {
				this.$checkAuthWithChapterId(courseId, chapterId).then((res) => {
					let hasAuth = res.data.data.hasAuth
					if (hasAuth === false && publicType === 1) {
						this.$message({
							message: '购买该课程后才能开始学习哦',
							type: 'error',
						})

						return
					} else {
						this.$router.push({
							path: '/course-play/' + courseId + '/' + chapterId,
						})
					}
				})
			}
		},
	},
}
