import { mapState, mapActions } from "vuex";
export default{
  data(){
    return{
      orderList:[],//加入购物车的商品
      allChecked:false,
      selectedProducts:[],//选中的商品
      tokens:'',
      count:0,
      price:0,
    }
  },
  created(){
    this.getShopCarList()
  },
  watch:{
    selectedProducts:{
      handler(newval,oldval){
        this.price = 0
        newval.forEach(item => {
          this.price += item.discountPrice * item.counter
        })
      },
      immediate: true
    },
  },
  computed:{
    getCount(){
      return this.selectedProducts.length
    },
  },
  methods:{
    ...mapActions(["saveCartNumAction"]),
    //全选按钮
    allCheck(){
      if(this.orderList.length === this.selectedProducts.length){
        this.allChecked = true
      }else{
        this.allChecked = false
      }
    },
    // 去结算
    getSelecteds(){
      if(!this.selectedProducts || this.selectedProducts.length <= 0){
        this.$message({
          type: "error",
          message: "请选择课程再结算！"
        })
        return;
      }
      let arr = new Array();
      this.selectedProducts.forEach(item => {
        arr.push({'number':item.counter,"id":item.courseId})
      })
      localStorage.setItem('selectedArr',JSON.stringify(arr))
      this.$router.push("/confirmOrder")
    },
    //全选
    selectAll(e){
      if(e){
        this.orderList.forEach(item => {
          item['checked'] = true
        })
        this.selectedProducts = this.orderList;
      }else{
        this.orderList.forEach(item => {
          item['checked'] = false
        })
        this.selectedProducts = []
      }
    },
    //数量 、价格变化
    selectSingleProduct(e, item){
      if(e){
        this.selectedProducts.push(item);
        this.allCheck()
      }else{
        for(let i = 0;i < this.selectedProducts.length;i++){
          if(item.id === this.selectedProducts[i].id){
            this.selectedProducts.splice(i,1);
          }
        }
        this.allCheck()
      }
    },
    //获取购物车数据
    getShopCarList(){
      this.$getShopCarList().then(res => {
        let list = res.data.list
        list.forEach(item => {
          item['checked'] = true
        })
        this.orderList = JSON.parse(JSON.stringify(list))
        this.selectedProducts = JSON.parse(JSON.stringify(list))
        this.allCheck()

      })
    },

    //删除购物车数据
    deleteOrder(id){
      this.$confirm('确定是否删除该课程', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$createToken().then(res => {
          this.tokens = res.data.token
          this.$deleteShopCar({id:id,token:this.tokens}).then(response => {
            if (response.meta.code === '200') {
              getShopCarCounter().then((res) => {
                if (res.meta.code == '200') {
                  this.saveCartNumAction(res.data.counter)
                } else {
                  this.$message({
                    message: res.meta.msg,
                    type: "error",
                  });
                }
              });
              this.$message({
                type: 'success',
                message: '删除成功'
              })
              this.getShopCarList()
            }
          })
        })
      }).catch(err => {})
    }
  }
}
