import imgCode from '@/api/globalImages.js'
export default{
    props:['userServiceAgreement','privateAgreement'],
    data(){
        return{
          webconfig: {
            id: "",//主键
            keywords: "",//站点关键字
            ext03: "",//扩展字段3
            ext02: "",//扩展字段2
            title: "",//网站标题
            logo: "",//网站LOGO
            ext01: "",//扩展字段1
            icp: "",//备案号
            copyright: "",//网站版权信息
            website: "",//网址
            description: ""//网站描述
          },
          guanfangwx:{
              imageName:'',
              imgUrl:''
          },
          teacherwx:{
              imageName:'',
              imgUrl:''
          }
        }
    },
    created(){
      this.__init()
      this.getImageByCodeGuanfangwx()
      this.getImageByCodeTeacherwx()
    },
    methods:{
      async __init(){
        let res = await this.$webConfig()
        this.webconfig = res.data.data
      },
      //跳转到隐私页面
      goAgreement(code){
        this.$router.push({
          path: '/agreement',
          query: {
            code: code,
          }
        })
      },
      getImageByCodeGuanfangwx(){
          this.$getImageByCode({imageCode:imgCode.global_guanfangcode}).then(res => {
            let data = res.data.data;
            this.guanfangwx = {
                imageName:data.imageName,
                imgUrl:data.imageUrl
            }
          })
      },
      getImageByCodeTeacherwx(){
          this.$getImageByCode({imageCode:imgCode.global_teachercode}).then(res => {
            let data = res.data.data;
            this.teacherwx = {
                imageName:data.imageName,
                imgUrl:data.imageUrl
            }
          })
      }
    }
}


